import React from 'react';
import "./search-box.style.css";

const Searchbox = ({ placeholder, handleChange }) => {
  return (
      
    <input
      className="search"
      type="search"
      placeholder={placeholder}
    //   onChange={(e) => {
    //     this.setState({ searchField: e.target.value });
    //   }}
    onChange = {handleChange}

    />
  );
};

export default Searchbox;
